import ScrollAnimation from "react-animate-on-scroll";
export default function Skill() {
    return (
        <div className="skill row justify-content-center">
            <div className="col-sm-3 col-12 d-flex justify-content-center header">
                <ScrollAnimation animateIn='animate__fadeIn '
                animateOut='animate__fadeOut' duration={.5}>
                        <h3 id="education-header">SKILLS</h3>
                </ScrollAnimation>
            </div>
            <div className="col-sm-5 col-10 skill-box">
                {
                    // Beginner
                    // Basic
                    // Intermediate 
                    // Advanced 
                }
                <ScrollAnimation animateIn='animate__fadeIn'
                animateOut='animate__fadeOut' duration={.5}>]
                    <p>HTML</p>
                    <div class="bar advance"></div>
                    <p>HTML</p>
                    <div class="bar intermediate"></div>
                    <p>HTML</p>
                    <div class="bar basic"></div>
                    <p>HTML</p>
                    <div class="bar beginner"></div>
                </ScrollAnimation>
            </div>
        </div>
    )
}